}

.cr-btn.primary {
  color: #fff;
  -webkit-box-shadow: inset 0 0 0 0.28rem #4ecca3;
  box-shadow: inset 0 0 0 0.28rem #4ecca3;
}

.cr-btn.primary:hover,
.cr-btn.primary:focus {
