.cr-btn.ex-padding {
  padding: 1.3rem 3.7rem 1rem;
}

body {
  overflow-x: hidden;
  -webkit-transition: all 500ms cubic-bezier(1, -0.02, 0.25, 0.75);
  transition: all 500ms cubic-bezier(1, -0.02, 0.25, 0.75);
}

body.menu-open {
  padding-left: 30rem;
}

.menu-toggle {
  position: fixed;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
  box-shadow: 5px 7px #4ecca3;
  z-index: 99999;
  width: 4rem;
  height: 4rem;
  cursor: pointer;
  left: 3.5rem;
  top: 50%;
  margin-top: -1.1rem;
  transition: all 500ms cubic-bezier(1, -0.02, 0.25, 0.75);
}

.menu-toggle .icon {
  position: absolute;
  display: inline-block;
  top: 1rem;
  /* left: 0; */
  width: 40px;
  height: 3px;
  background-color: #4ecca3;
  transition: all 0.4s ease;
}
.menu-toggle:hover {
  box-shadow: 4px 4px #4ecca3;
}

.menu-toggle .icon:before,
.menu-toggle .icon:after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #4ecca3;
  -webkit-transform-origin: center;
  -moz-transform-origin: center;
  -ms-transform-origin: center;
  transform-origin: center;
  -webkit-transition: all 0.4s ease;
  transition: all 0.4s ease;
}

.menu-toggle .icon:before {
  top: 1rem;
}

.menu-toggle .icon:after {
  top: 2rem;
}

.menu-toggle.open {
  left: 29.1rem;
}

.menu-toggle.open .icon {
  background-color: transparent;
}

.menu-toggle.open .icon:before {
  -webkit-transform: rotate(45deg) scaleX(1.4);
  -moz-transform: rotate(45deg) scaleX(1.4);
  -ms-transform: rotate(45deg) scaleX(1.4);
  transform: rotate(45deg) scaleX(1.4);
}

.menu-toggle.open .icon:after {
  top: 1rem;
  -webkit-transform: rotate(-45deg) scaleX(1.4);
  -moz-transform: rotate(-45deg) scaleX(1.4);
  -ms-transform: rotate(-45deg) scaleX(1.4);
  transform: rotate(-45deg) scaleX(1.4);
}

.main-menu {
  position: fixed;
  top: 0;
  left: -30rem;
  height: 100%;
  width: 30rem;
  background-color: #393e46;
  text-align: right;
