.header {
  background-color: #232931;
  text-align: center;
  padding: 16rem 3.5rem 14rem;
}

.header .mouse-scroll {
  position: absolute;
  display: inline-block;
  bottom: 3rem;
  left: 50%;
  margin-left: -0.8rem;
  width: 1.6rem;
  height: 2.4rem;
  border-radius: 2rem;
  background-color: rgba(255, 255, 255, 0.15);
  pointer-events: none;
  -webkit-box-shadow: 0 0.3rem 5rem rgba(0, 0, 0, 0.24);
  box-shadow: 0 0.3rem 5rem rgba(0, 0, 0, 0.24);
  -webkit-transition: all 0.6s ease;
  transition: all 0.6s ease;
}

.header .mouse-scroll:before {
  position: absolute;
  content: "";
  top: 0.5rem;
  left: 50%;
  margin-left: -0.1rem;
  width: 0.2rem;
  height: 0.4rem;
  border-radius: 1rem;
  background-color: rgba(255, 255, 255, 0.21);
  -webkit-animation: m-an 2s ease infinite;
  -moz-animation: m-an 2s ease infinite;
  -ms-animation: m-an 2s ease infinite;
  animation: m-an 2s ease infinite;
}

.header .mouse-scroll.hide {
  opacity: 0;
}

.header .mouse-scroll.hide:before {
  opacity: 0;
  -webkit-animation: none;
  -moz-animation: none;
  -ms-animation: none;
  animation: none;
  -webkit-transition: all 0.6s ease;
  transition: all 0.6s ease;
}

@-webkit-keyframes m-an {
  0% {
    top: 0.5rem;
    height: 0.2rem;
    opacity: 0;
  }

  15% {
    top: 0.7rem;
    height: 0.6rem;
    opacity: 1;
  }

  50% {
    top: 0.9rem;
    height: 0.6rem;
    opacity: 1;
  }

  70% {
    top: 1rem;
    height: 0.5rem;
    opacity: 1;
  }

  90% {
    top: 1.2rem;
    height: 0.5rem;
    opacity: 0;
  }

  100% {
    top: 1.2rem;
    height: 0.5rem;
    opacity: 0;
  }
}

